const Category = ({ id, go, fetchedUser, targetCategory, setActiveItem }) => {
    const [subcat, setSubcat] = useState(targetCategory.subcat);
    const [activeSubcat, setActiveSubcat] = useState(null);
    return (
        <Panel id={id}>
            <PanelHeader left={<PanelHeaderBack onClick={()=>go('home')} />}/>
            <Placeholder
                icon={<div style={{
                    background: 'url('+targetCategory.icon+') no-repeat',
                    height: 50,
                    width: 50
                }}/>}
            >
                Выбрана категория: {targetCategory.title}
            </Placeholder>
            {
                targetCategory.subcat &&
                <Group header={<Header mode="secondary">Выберите тип товара</Header>}>
                    <HorizontalScroll>
                        <Tabs>
                            {
                                targetCategory.subcat.map(subcat=>{
                                    return (
                                        <TabsItem
                                            onClick={() => {
                                                setActiveSubcat(subcat)
                                            }}
                                            selected={activeSubcat === subcat}
                                        >
                                            {subcat}
                                        </TabsItem>
                                    )
                                })
                            }
                        </Tabs>
                    </HorizontalScroll>
                </Group>
            }
            {
                activeSubcat !== null &&
                <MiniInfoCell textWrap={'full'}>Ниже будут выводиться карточки с - {activeSubcat}</MiniInfoCell>
            }
            <Div>
                <CardGrid>
                    {
                        cart.map(item=>{
                            return(
                                <Card
                                    onClick={()=>{
                                        setActiveItem(item)
                                        go('aboutItem')
                                    }}
                                    style={{padding: 2, borderRadius: 13, margin: 0, backgroundColor: '#007151'}}
                                    size="m"
                                    mode="shadow"
                                >
                                    <div style={{
                                        height: 150,
                                        backgroundImage: 'url(' + item.img + ')',
                                        backgroundSize: 'cover',
                                        backgroundPosition: 'center 35%',
                                        backgroundRepeat: 'no-repeat',
                                        borderRadius: 13
                                    }}>
                                    </div>
                                    <MiniInfoCell textWrap={'full'}><Caption level={1}>{item.name}</Caption></MiniInfoCell>
                                    <MiniInfoCell before={<Icon24MoneyCircle height={20} width={20}/>}><Subhead>{item.price} руб.</Subhead></MiniInfoCell>
                                    <MiniInfoCell><Button size="m" stretched mode="outline">В корзину</Button></MiniInfoCell>
                                </Card>
                            )
                        })
                    }
                </CardGrid>
            </Div>
            <Group header={<Header mode="secondary">Есть вопросы?</Header>}>
                <MiniInfoCell before={<Icon24Phone height={20} width={20}/>}><Link href="tel:+79022954808" target="_blank">+7 (902) 294-48-08</Link></MiniInfoCell>
                <MiniInfoCell before={<Icon24LogoVk height={20} width={20}/>}><Link href="https://vk.com/zoomagasin" target="_blank">vk.com/zoomagasin</Link></MiniInfoCell>
                <MiniInfoCell before={<Icon24Linked height={20} width={20}/>}><Link href="https://zoomagasin.ru/" target="_blank">zoomagasin.ru</Link></MiniInfoCell>
                <Footer/>
            </Group>
        </Panel>
    )
};

export default Category;
